body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
    font-family: 'Nanum Gothic',
        sans-serif;
}

p {
    font-family: 'Nanum Gothic',
        sans-serif;
    font-size: 23px;
}

.app {
    padding: 70px;
}

.navi {
    opacity: 1;
    background-color: #FFDC00;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 30px;
}

.navi:hover {

    text-align: center;
    background-color: #FFFFFF;
}
.visible.transition {

    margin-top: 0 !important;
    display: inline-block !important;
    position: relative;
    top: 0%;
    margin-left: auto;
    margin-right: auto;
}
:root {
    --main-color: #7e96da;
    --main-user-color: #d05eca;
    --secondary-color: white;
    --main-text-color: #3e5869;
    --secondary-text-color: #b0c7d6;
    --send-message-form: #F5F5F5;
}


.message-user .message-text {
    background: #d05eca;
    background: var(--main-user-color);
    color: white;
    color: var(--secondary-color);
    display: inline;
    padding: 4px 8px;
    border-radius: 8px;
    margin: 15px 0;


}

.message-bot .message-text-bot {
    background: #7e96da;
    background: var(--main-color);
    color: white;
    color: var(--secondary-color);
    display: inline;
    padding: 4px 8px;
    border-radius: 8px;


}
.message-list {
    height: 400px;
    overflow: scroll;
    background: var(--secondary-color);
}
:root {
    --main-color: #7e96da;
    --main-user-color: #d05eca;
    --secondary-color: white;
    --main-text-color: #3e5869;
    --secondary-text-color: #b0c7d6;
    --send-message-form: rgb(253, 234, 125);
    --send-message-form-chat: rgb(255, 255, 255);
}




.send-message-form {
    background: rgb(253, 234, 125);
    background: var(--send-message-form);
    display: flex;
}


.send-message-form .onChat {
    width: 100%;
    padding: 15px 10px;
    margin: 0;
    border-style: none;
    background: rgb(255, 255, 255);
    background: var(--send-message-form-chat);
    font-weight: 200;

}

.send-message-form .initChat {
    width: 100%;
    padding: 15px 10px;
    margin: 0;
    border-style: none;
    background: rgb(253, 234, 125);
    background: var(--send-message-form);
    font-weight: 200;

}

.send-message-form input:focus {
    outline-width: 0;
}

.send-message-form input::-webkit-input-placeholder {
    color: #3e5869;
    color: var(--main-text-color);
}

.send-message-form input::-ms-input-placeholder {
    color: #3e5869;
    color: var(--main-text-color);
}

.send-message-form input::placeholder {
    color: #3e5869;
    color: var(--main-text-color);
}
.options {
    text-align: center;
}

.options .nobull {
    list-style-type: none;
    padding-left: 0;
}
.lc-container {

    display: flex;
}

.lc-first-col {
    background-color: grey;
    width: 65%;
}

.lc-second-col {
    width: 35%;
}
.footer {
    background-color: #FFFFFF;
    height: 170px;
    padding-left: 100px;
    padding-right: 100px;
    padding-top: 70px;
    display: flex;
    justify-content: space-around;
}
.profile {
    background-color: #FFDC00;
    padding: 40px;
    font-family: 'Nanum Gothic',
        sans-serif;
    font-size: 13px;
}
